'use strict';

(function () {
  'use strict';

  angular.module('app').factory('SellersPerDayService', SellersPerDayService);
  function SellersPerDayService($http, $q, API_ENDPOINT, $localStorage) {
    return {
      getRoutesBySeller: getRoutesBySeller,
      getSellers: getSellers
    };

    function getRoutesBySeller(daySelected, userId) {
      var deferred = $q.defer();
      daySelected = daySelected.toISOString().split("T")[0];
      $http.get(API_ENDPOINT + '/history/sellers?since=' + daySelected + '&until=' + daySelected + '&userId=' + userId, {
        headers: {
          userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
          ci: $localStorage['usr'].ci
        }
      }).then(function (response) {
        deferred.resolve(response.data);
      }, function (response) {
        deferred.reject(response);
      });
      return deferred.promise;
    }

    function getSellers() {
      var deferred = $q.defer();
      $http.get(API_ENDPOINT + "/users?roleFilter=visitador", {
        headers: {
          userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
          ci: $localStorage['usr'].ci
        }
      }).then(function (response) {
        var sellers = response.data.data.users;
        deferred.resolve(sellers);
      }, function (response) {
        deferred.reject(response);
      });
      return deferred.promise;
    }
  }
})();


'use strict';

(function () {
  'use strict';

  angular.module('app').component('sellersPerDay', {
    controller: sellersPerDayController,
    controllerAs: 'vm',
    templateUrl: 'app/history/sellersPerDay/sellersPerDay.view.html'
  });

  /** @ngInject */
  function sellersPerDayController(SellersPerDayService, $filter, ngDialog, $scope) {
    var vm = this;
    vm.sellersList = [];
    vm.selectSeller = selectSeller;
    vm.sellerSelected = {};
    vm.daySelected = '';
    vm.markers = [];
    vm.roadTrip;

    vm.getRoutesBySeller = getRoutesBySeller;

    var map = void 0;

    /////
    function initialize() {
      getSellers();
      var mapOptions = {
        zoom: 12,
        center: new google.maps.LatLng(-16.5069882, -68.136291),
        mapTypeId: 'roadmap'
      };
      map = new google.maps.Map(document.getElementById('map'), mapOptions);
    }

    function getRoutesBySeller() {
      if (!vm.daySelected) return false;
      if (vm.roadTrip) {
        vm.roadTrip.setMap(null);
        vm.roadTrip = {};
      }
      var roadTripCoordinates = [];
      return SellersPerDayService.getRoutesBySeller(vm.daySelected, vm.sellerSelected.ci).then(function (response) {
        if (response.length === 0) {
          notExistCoordinatesWarning();
        }
        _.each(response, function (value, key) {
          if (parseFloat(value.latitude) && parseFloat(value.longitude)) {
            roadTripCoordinates.push({ lat: parseFloat(value.latitude), lng: parseFloat(value.longitude), userId: value.user_id, timestamp: value.timestamp });
          }
        });

        vm.roadTrip = new google.maps.Polyline({
          path: roadTripCoordinates,
          strokeColor: '#FFFF1',
          strokeOpacity: 1.0,
          strokeWeight: 2
        });

        vm.roadTrip.setMap(map);
        setMarkers(roadTripCoordinates[0], roadTripCoordinates[roadTripCoordinates.length - 1]);
      });
    }

    function setMarkers(firstCoord, lastCoord) {
      _.forEach(vm.markers, function (marker, index) {
        vm.markers[index].setMap(null);
      });
      vm.markers = [];
      vm.test = $filter('date')(firstCoord.timestamp, 'medium');
      vm.markers.push(newMarker(firstCoord));
      vm.markers.push(newMarker(lastCoord));
      _.forEach(vm.markers, function (marker, index) {
        marker.addListener('click', function () {
          map.setZoom(16);
          map.setCenter(marker.getPosition());
        });
      });
    }

    function newMarker(obj) {
      var myLatLng = new google.maps.LatLng(obj.lat, obj.lng);
      return new google.maps.Marker({
        position: myLatLng,
        map: map,
        label: {
          color: 'black',
          fontWeight: 'bold',
          text: $filter('date')(obj.timestamp, 'medium')
        },
        icon: {
          labelOrigin: new google.maps.Point(11, 50),
          url: '../../../assets/images/markers/marker_red.png',
          size: new google.maps.Size(22, 40),
          origin: new google.maps.Point(0, 0),
          anchor: new google.maps.Point(11, 40)
        }
      });
    }

    function getSellers() {
      return SellersPerDayService.getSellers().then(function (response) {
        vm.sellersList = response;
      });
    }

    function selectSeller(seller) {
      _.forEach(vm.sellersList, function (obj) {
        if (obj.ci !== seller.ci) {
          obj.active = false;
        }
      });

      vm.sellerSelected = seller.active ? seller : {};
      vm.daySelected = '';
    }

    function notExistCoordinatesWarning() {
      var notExistCoordinatesWarningModal = ngDialog.open({
        disableAnimation: true,
        closeByDocument: true,
        showClose: true,
        template: 'app/history/sellersPerDay/notExistCoordinates.modal.html',
        scope: $scope
      });
    }
    initialize();
  }
})();


'use strict';

(function () {
  'use strict';

  angular.module('app').factory('ReportsService', ReportsService);
  angular.module('app').directive('exportToCsv', exportToCsv);
  function ReportsService($http, $q, API_ENDPOINT, $localStorage) {
    return {
      getSellers: getSellers,
      getCoordinatesReport: getCoordinatesReport,
      getStatusGpsPerSeller: getStatusGpsPerSeller,
      getRoles: getRoles,
      getUsers: getUsers,
      bestSellers: bestSellers,
      getOrders: getOrders,
      getLogs: getLogs,
      getClients: getClients
    };

    function getSellers() {
      var deferred = $q.defer();
      $http.get(API_ENDPOINT + '/users?roleFilter=visitador', {
        headers: {
          userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
          ci: $localStorage['usr'].ci
        }
      }).then(function (response) {
        var list = response.data.data.users;
        _.forEach(list, function (seller, index) {
          list[index]['label'] = seller.firstName + ' ' + seller.firstSurname;
        });
        deferred.resolve(list);
      }, function (response) {
        deferred.reject(response);
      });
      return deferred.promise;
    }

    function getCoordinatesReport(userId, since, until) {
      var deferred = $q.defer();
      since = since.toISOString().split("T")[0];
      until = until.toISOString().split("T")[0];
      $http.get(API_ENDPOINT + '/history/sellers?since=' + since + '&until=' + until + '&userId=' + userId, {
        headers: {
          userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
          ci: $localStorage['usr'].ci
        }
      }).then(function (response) {
        deferred.resolve(response.data);
      }, function (response) {
        deferred.reject(response);
      });
      return deferred.promise;
    }

    function getStatusGpsPerSeller(userId, since, until) {
      var deferred = $q.defer();
      since = since.toISOString().split("T")[0];
      until = until.toISOString().split("T")[0];
      $http.get(API_ENDPOINT + '/reports/getStatusGpsPerSeller?since=' + since + '&until=' + until + '&userId=' + userId, {
        headers: {
          userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
          ci: $localStorage['usr'].ci
        }
      }).then(function (response) {
        deferred.resolve(response.data);
      }, function (response) {
        deferred.reject(response);
      });
      return deferred.promise;
    }

    function getRoles() {
      var deferred = $q.defer();
      $http.get(API_ENDPOINT + '/roles', {
        headers: {
          userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
          ci: $localStorage['usr'].ci
        }
      }).then(function (response) {
        deferred.resolve(response.data.data.roles);
      }, function (response) {
        deferred.reject(response);
      });
      return deferred.promise;
    }

    function getUsers() {
      var deferred = $q.defer();
      $http.get(API_ENDPOINT + '/users', {
        headers: {
          userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
          ci: $localStorage['usr'].ci
        }
      }).then(function (response) {
        deferred.resolve(response.data.data.users);
      }, function (response) {
        deferred.reject(response);
      });
      return deferred.promise;
    }

    function bestSellers(since, until) {
      var deferred = $q.defer();
      since = since.toISOString().split("T")[0];
      until = until.toISOString().split("T")[0];
      var users = [];

      getUsers().then(function (response) {
        users = response;
        $http.get(API_ENDPOINT + '/reports/bestSellers?since=' + since + '&until=' + until, {
          headers: {
            userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
            ci: $localStorage['usr'].ci
          }
        }).then(function (response) {
          var sellerList = response.data.data;
          var bestSellers = [];
          sellerList = _.groupBy(sellerList, 'user_id');
          _.forEach(sellerList, function (items, index) {
            var ordersCount = items.length,
                totalAmount = 0;
            _.forEach(items, function (order, index) {
              totalAmount = totalAmount + order.total;
            });
            bestSellers.push({
              user_id: index,
              user: _.find(users, function (item) {
                return item.ci === index;
              }),
              ordersCount: ordersCount,
              orders: items,
              totalAmount: totalAmount
            });
          });
          deferred.resolve(_.orderBy(bestSellers, ['totalAmount'], ['desc']));
        }, function (response) {
          deferred.reject(response);
        });
      });

      return deferred.promise;
    }

    function getOrders(since, until) {
      var deferred = $q.defer();
      since = since.toISOString().split("T")[0];
      until = until.toISOString().split("T")[0];
      var users = [];

      getUsers().then(function (response) {
        users = response;
        $http.get(API_ENDPOINT + '/reports/orders?since=' + since + '&until=' + until, {
          headers: {
            userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
            ci: $localStorage['usr'].ci
          }
        }).then(function (response) {
          var list = response.data.data;
          _.forEach(list, function (item) {
            item['user'] = _.find(users, function (user) {
              return user.ci === item.userId;
            });
          });
          deferred.resolve(list);
        }, function (response) {
          deferred.reject(response);
        });
      });

      return deferred.promise;
    }

    function getLogs() {
      var deferred = $q.defer();
      $http.get(API_ENDPOINT + '/reports/logs', {
        headers: {
          userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
          ci: $localStorage['usr'].ci
        }
      }).then(function (response) {
        deferred.resolve(response.data.data);
      }, function (response) {
        deferred.reject(response);
      });
      return deferred.promise;
    }

    function getClients(searchTerm, debtClient) {
      var deferred = $q.defer();
      var keyDebt = debtClient ? '&debt=' + debtClient : '';
      $http.get(API_ENDPOINT + '/clients' + '?like=' + searchTerm.trim() + keyDebt, {
        headers: {
          userName: $localStorage['usr'].firstName + ' ' + $localStorage['usr'].firstSurname,
          ci: $localStorage['usr'].ci
        }
      }).then(function (response) {
        deferred.resolve(response.data);
      }, function (response) {
        deferred.reject(response);
      });
      return deferred.promise;
    }
  }

  function exportToCsv() {
    return {
      restrict: 'A',
      ngTable: '=',
      scope: { reportname: '@' },
      link: function link(scope, element, attrs) {
        var el = element[0];
        element.bind('click', function (e) {
          var table = angular.element(document.getElementById('#reportTable'))[0];
          var csvString = '';
          for (var i = 0; i < table.rows.length; i++) {
            var rowData = table.rows[i].cells;
            for (var j = 0; j < rowData.length; j++) {
              csvString = csvString + rowData[j].innerHTML + ",";
            }
            csvString = csvString.substring(0, csvString.length - 1);
            csvString = csvString + "\n";
          }
          csvString = csvString.substring(0, csvString.length - 1);
          var a = $('<a/>', {
            style: 'display:none',
            href: 'data:application/octet-stream;base64,' + btoa(csvString),
            download: scope.reportname ? scope.reportname : 'report.csv'
          }).appendTo('body');
          a[0].click();
          a.remove();
        });
      }
    };
  }
})();


